<template>
  <section
    v-if="entries"
    class="content"
  >
    <pageEntry :pageTitle="entries[0].title" />
    <div class="container">
      <div class="row">
        <div
          class="col-12"
          v-html="entries[0].body"
        ></div>
      </div>
    </div>
    <Form />
  </section>
</template>

<script>
import Form from "@/components/Common/Form.vue";
import pageEntry from "@/components/Common/PageEntry.vue";
import { gql } from "apollo-boost";
export default {
  created() {},
  apollo: {
    entries: gql`
      query {
        entries(slug: "soft-fue-2") {
          title
          ... on pages_pages_Entry {
            title
            body
          }
        }
      }
    `
  },
  components: {
    pageEntry,
    Form
  }
};
</script>

